import React, {useState} from "react";
import './App.css';
import Jumbotron from "./Components/Jumbotron";
import NavigationBar from "./Components/NavigationBar";
import ProductList from "./Components/ProductList";
import axios from "axios";
import Loading from "./Components/Loading"

import {Link, Route, Switch} from "react-router-dom";
import ProductDetail from "./Components/ProductDetail";
import Data from "./data";


function App() {

  let[shoes, changeshoes] = useState(Data);
  let [stock, changestock] = useState([10,11,12])

  return (
    <div className="App">

    <NavigationBar></NavigationBar>

      <Switch>
      <Route exact path="/">
        <Jumbotron/>
        <ProductList shoes = { shoes } ></ProductList>
        <button className="btn btn-primary" onClick={() => {

          <Loading/>//Loading....before axiox.get()

          // how to use axios.post
          // axios.post('server url', {id : 'bini9394', pw: dabin }).then()


          axios.get('https://codingapple1.github.io/shop/data2.json')
            .then((result)=>{

              //It's loaded, remove the function

              let copy = [...shoes]
                copy.push(...result.data)
                changeshoes(copy) // === () = > {changeshoes([...shoes, ...result.data])
            }) //when it's succeed
            .catch(()=>{
              console.log('failed it')
            }) //when it's failed
          ;
        }}>see more</button>
      </Route>

      <Route path='/detail/:id'>
        <ProductDetail shoes={ shoes } stock = {stock} changestock = {changestock}></ProductDetail>
      </Route>



    <Route path="/:id">
      <div>what</div>
    </Route>

      </Switch>

    </div>
  );
}

export default App;
